.cardContent {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-height: 100%;
  max-width: 100%;
  flex-grow: 1;
  --textSpace: 40px;
  --buttonSpace: 40px;
  --imgRowBorder: 10px;
  --imgSpace: calc(
    var(--contentSpace) - var(--textSpace) - var(--buttonSpace) -
      (2 * var(--imgRowBorder)) /* 250 = 350-100 */
  );
}
.cardImgs {
  display: flex;
  margin: var(--imgRowBorder) 0;
  max-height: calc(var(--imgSpace));
  height: calc(var(--imgSpace));
}
.cardImg {
  margin: 0 20px;
  height: 100%;
}
.cardText {
  display: flex;
  max-height: var(--textSpace);
}
.categoria {
  display: flex;
  align-items: center;
  width: 30%;
  height: var(--textSpace);
}
.textoEnvio {
  height: var(--textSpace);
  width: 70%;
  display: flex;
  align-items: center;
}
.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.9); /* Black w/ opacity */
}
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}
.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}
.modal-image {
  margin: auto;
  display: block;
  height: 70%;
  max-height: 700px;
  width: auto;
}

.cardButtons {
  display: flex;
  justify-content: space-between;
}
.cardButtons button {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: var(--buttonSpace);
  height: var(--buttonSpace);
  border-radius: 40px;
  border: 2px solid #aaa;
  transition: filter 300ms;
}
.cardButtons button:hover {
  filter: brightness(1.1);
}
.aceitarSaude {
  background: #e0e0e0;
  padding-top: 5px;
  width: 10%;
}
.preRejBox {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.rejeitar {
  background: #e0e0e0;
  width: calc(10% * 10 / 9);
  margin-right: 1%;
}
.rejeitando {
  background: #be0000;
  width: calc(10% * 10 / 9);
}
.rejBox {
  width: 90%;
  display: flex;
  align-items: center;
}
.rejTextBox {
  display: flex;
}
.textFix {
  background-color: #f1f1f1;
  padding: 0 0 0 4px;
  margin: 0 0 0 10px;
  height: 30px;
  width: 115px;
  display: flex;
  align-items: center;
}

.textBox {
  background-color: #f1f1f1;
  padding: 0 0 0 5px;
  margin: 0 0 0 2px;
  border: 0;
  height: 30px;
  width: calc(
    95% - 136px - (10% * 10 / 9)
  ); /* 115 do tamanho do texto fixo e o rstante Ã© a soma das margens e pads do texto e dessa caixa */
  font-size: large;
  text-decoration-style: none;
  text-decoration: none;
}
